*{
  margin: 0px;
  padding: 0px;
  font-family: 'Poppins', sans-serif;
}
body{
  background: url(./images/bg.jpg);
  background-repeat: repeat;
}
.header{
  display: inline-flex;
  justify-content: space-around;
  align-items: center;
  flex-wrap: wrap;
  width: 98vw;
  height: 100vh;
}
.heading{
  letter-spacing: .7px;
}
.heading-img img{
  width: 300px;
  clip-path: circle(50%);
}
.heading h1{
  letter-spacing: 1px;
}
.heading span{
  color: #2EE59D;
  text-align: center;
}
.skill-flex{
  display: inline-flex;
  justify-content: space-around;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}
.progress-container{
  display: inline-flex;
  justify-content: start;
  width: 500px;
  margin: 10px;
}
.CircularProgressbar{
  width: 110px;
}
.progress-text{
  padding: 30px;
}
.progress-text p{
  color: grey;
}
.row{
  display: inline-flex;
  flex-wrap: wrap;
  flex-direction: row;
}
.footer-container{
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  color: white;
  flex-direction: column;
  background-color: rgb(60, 60, 60);
  padding: 30px 0px;
  text-align: center;
}
.footer-container .footer-content{
  display: inline-flex;
}
.footer-container .footer-content a{
  text-decoration: none;
  display: inline-flex;
  justify-content: center;
  align-items: center;
  margin: 20px 10px;
  color: white;
  border: 1px solid aqua;
  width: 100px;
  height: 40px;
}

.contact-form{
  width: 100%;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  flex-wrap: wrap;
  flex-direction: column;
}
.contact-form form{
  width: 340px;
}
.contact-form p{
  margin-bottom: 3px;
}
.contact-form input, .contact-form textarea{
  width: 100%;
  border: none;
  outline: none;
  margin-bottom: 8px;
  padding-left: 10px;
  background-color: rgb(60, 60, 60);
  color: #f2f3f4;
}
.contact-form input{
  height: 35px;
}
.contact-form textarea{
  padding-top: 10px;
  color: #f2f3f4;
}
.contact-form input::placeholder, .contact-form textarea::placeholder{
  color: #f2f3f4;
}
.contact-form input:focus,.contact-form textarea:focus{
  background-color: #f2f3f4;
  color: #1B1B1B;
}
.contact-form input[type=submit]{
  float: right;
  width: 40%;
  background-color: crimson;
  color: #f2f3f4;
  font-weight: bold;
  cursor: pointer;
  transition: .5s;
  padding-left: 0px;
  margin-right: -10px;
}

.contact-form input[type=submit]:hover {
  animation: jelly 0.5s;
}
@keyframes jelly {
  25% {
    transform: scale(0.9, 1.1);
  }
  50% {
    transform: scale(1.1, 0.9);
  }
  75% {
    transform: scale(0.95, 1.05);
  }
}

